import React from 'react';
import style from './DropMenu.module.css';
import { HashLink as NavLink } from 'react-router-hash-link';

class MenuDrop extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            dropMenu: false
        }
    }

    componentDidMount(){
        //     
        window.addEventListener('scroll', () => {
            const isTop = window.scrollY < 200;
            const onTop = window.scrollY < 4;
            if(isTop == false){
                this.setState({ dropMenu: true });
            } else if(onTop == true){
                this.setState({ dropMenu: false });
            }
        });
    };

    render() {
        return (
            <ul className={style.menu}>
                <li className={style.menuItem}>
                    <NavLink smooth to='/main/#top' activeClassName={style.active}></NavLink>
                    <ul className={this.state.dropMenu ? style.dropMenu : style.menuDrop}>
                        <li>
                            <NavLink smooth to="/main/#mission"> </NavLink>
                        </li>
                        <li>
                            <NavLink smooth to="/main/#whyFilbert"></NavLink>
                        </li>
                        <li>
                            <NavLink smooth to="/main/#scheme"></NavLink>
                        </li>
                        <li>
                            <NavLink smooth to="/main/#etiquette"></NavLink>
                        </li>
                        <li>
                            <NavLink smooth to="/main/#delivery"></NavLink>
                        </li>
                    </ul>
                </li>
                <li className={style.menuItem}>
                    <NavLink smooth to='/information/#top' activeClassName={style.active}> </NavLink>
                    <ul className={this.state.dropMenu ?  style.dropMenu : style.menuDrop}>
                        <li>
                            <NavLink smooth to="/information/#news"></NavLink>
                        </li>
                        <li>
                            <NavLink smooth to="/information/#announsments"></NavLink>
                        </li>
                        <li>
                            <NavLink smooth to="/information/#newWorker"> </NavLink>
                        </li>
                        <li>
                            <NavLink smooth to='/jobs/#top'></NavLink>
                        </li>
                    </ul>
                </li>
                <li className={style.menuItem}>
                    <NavLink smooth to='/corp_live/#top' activeClassName={style.active}> </NavLink>
                    <ul className={this.state.dropMenu ?  style.dropMenu : style.menuDrop}>
                        <li>
                            <NavLink smooth to='/birthdays'> </NavLink>
                        </li>
                        <li>
                            <NavLink smooth to='/tournament/#top'></NavLink>
                        </li>
                    </ul>
                </li>
            </ul>
        )
    }
}

export default MenuDrop;